
.card {
  height: 100%;
  width: 100%;
  background: #fff;

  border-radius: 24px;
  box-shadow: -10px 10px 20px rgba(0, 0, 0, 0.5);
  transition: transform $transition_long;
    
  &:hover {
    transform: scale(1.03);
  }

  &__tittle {
    @include font (600, 13px, 19px, #56ccf2, $font-tittle);
    text-transform: uppercase;

    box-sizing: border-box;
    display: inline-block;
    border-radius: 24px;
    padding: 2px 10px;
    margin: 34px 42px 0;

    &--blue {
      color: #56ccf2;
      background-color: rgba(86, 204, 242, 0.1);
    }

    &--red {
      color: #f36363;
      background-color: rgba(243, 99, 99, 0.1);
    }

    &--green {
      color: #15d850;
      background-color: rgba(21, 216, 80, 0.1);
    }

    &--yellow {
      color: #fcc91d;
      background-color: rgba(252, 201, 29, 0.1);
    }
  }

  &__text {
    @include font (600, 13px, 150%, $text_main_color, $font-text);
    display: inline-block;
    margin: 24px 32px 0;
  }

  &__button {
    display: flex;
    align-items: center;
  }

  &__circle {
    display: inline-block;
    width: 40px;
    height: 40px;

    background: url(../../images/arr-button.svg) no-repeat center;
    background-size: 16px 16px;
    border-radius: 50%;
    margin: 32px;
    transition: $transition_fast transform;

    &:hover {
      transform: scale(1.2);
    }

    &--blue {
      background-color: #56ccf2;
    }

    &--red {
      background-color: #f36363;
    }

    &--green {
      background-color: #15d850;
    }

    &--yellow {
      background-color: #fcc91d;
    }
  }

  &__c-text {
    @include font (600, 15px, 22px, #334563, $font-tittle);
    display: inline-block;
    text-decoration: none;
    outline: none;
    transition: transform $transition_long;
    
    &:hover {
      transform: scale(1.03);
    }

  }

  //end
}
