


.button {
  @include font (600, 15px, 22px, $head_main_color, $font-tittle);
  width: 226px;
  height: 50px;

  background: #ff9c8e;
  outline: none;

  border-radius: 64px;
  border-style: none;
  transition: $transition_long background,
    $transition_long transform;

  &:hover {
    background: #ff8e9d;
    transform: scale(1.05);
  }
}

.tittle {
  @include font (600, 52px, 150%, $text_tittle_color, "Poppins");
  text-align: center;
  padding-top: 128px;
}

@media only screen and (max-width: 300px) {
  button {
    width: 60%;
    z-index: 9;
  }
}
