
.about {

  &__content {
    margin: 100px auto 50px;
    max-width: 760px;
    width: 90%;
  }

  &__tittle {
    @include font (600, 32px, 150%, $text_tittle_color, $font-tittle);
    align-items: center;
  }

  &__text {
    @include font (600, 24px, 150%, $text_main_color, $font-text);

  }
}
